@import "../../global.scss";

.testimonials{
    background-color: white;
    display: flex;
    align-items: center;
    flex-direction: column;

    @include mobile{
        justify-content: space-around;
        }

    h1{
        font-size: 50px;

        @include mobile{
            font-size: 20px;
        }
    }
    .container{
      
    width: 100%;
    height: 80%;
    display: flex;
    align-items: center;
    justify-content: center;


    @include mobile{
            flex-direction: column;
            height: 100%;
            
    }


    .card{
        width: 350px;
        height: 70%;
        border-radius: 10px;
        box-shadow: 0px 0px 15px -8px black;
        display: flex;
        justify-content: space-around;
        flex-direction: column;
        padding: 20px;
        transition: all 1s ease;

        @include mobile{
            height: 180px;
            margin: 10px 0;
        }
        
        &.featured{
            width: 300px;
            height: 75%;
            margin: 0 30px;

            @include mobile{
                height: 180px;
                width: 250px;
                margin: 5px;
            }
        }

        &:hover{
            transform: scale(1.1);
        }




        .top{
            display: flex;
            align-content: center;
            justify-content: center;
            
            img{
                &.left, &.right{
                    height: 25px;
                }
                &.user{
                    height: 60px;
                    width: 60px;
                    border-radius: 50%;
                    object-fit: cover;
                    margin: 0 30px;

                    @include mobile{
                        width: 30px;
                        height: 30px;
                    }
                }
            }
        }
        .center{
            padding: 10px;
            border-radius: 10px;
            background-color: #f9e4e8;

            @include mobile{
                font-size: 12px;
                padding: 5px;
            }
        }
        .bottom{
            display: flex;
            align-items: center;
            flex-direction: column;
            justify-content: center;
            h3{
                margin-bottom: 5px;

                @include mobile{
                font-size: 14px;
                }
            }
            h4{
                color: grey;

                @include mobile{
                    font-size: 13px;
                    }
            }
        }
        }



}
}



























